const Stats = (props) => {
  const { spins, bank } = stats;

  return (
    <div className="w-30 flx fade-t stats">
      <div className="flx flx-jc-ce flx-ai-ce p-20 brd brd-wht1 w-50 m-r-2">
        <p className="txt-wht mont">{spins.label}</p>
        <p className="txt-wht mont m-l-auto">{props.spins}</p>
      </div>

      <div className="flx flx-jc-ce flx-ai-ce p-20 brd brd-wht1 w-50 m-l-2">
        <p className="txt-wht mont">{bank.label}</p>
        <p className="txt-wht mont m-l-auto">{props.bank}</p>
      </div>
    </div>
  );
};

const Spinner = (props) => {
  const { spins, bank } = props.stat;
  const { state, set, setStat, stat } = props;

  return (
    <section className="w-100 h-100 flx flx-col flx-jc-sp flx-ai-ce">
      <Stats spins={spins} bank={bank} />
      <Svg
        stat={stat}
        state={state}
        set={set}
        setStat={setStat}
        spins={spins}
        bank={bank}
      />
      <small className="w-30 txt-wht txt-al-ce lato">{window.txt.terms}</small>
    </section>
  );
};

export { Spinner };
